import React, { useEffect } from "react";

import { makeStyles } from "@material-ui/core/styles";
import AddIcon from '@material-ui/icons/Add';
import GridContainer from '../../shared/Grid/GridContainer';
import GridItem from '../../shared/Grid/GridItem';
import Card from "../../shared/Card/Card";
import CardHeader from "../../shared/Card/CardHeader.js";
import CardBody from "../../shared/Card/CardBody.js";
import { Loader } from "../../shared/Loader/loader";
import LeadForm from "./leadForm";
import { SideDrawer } from "../../shared/SideDrawer/drawer.js";
import Button from '@material-ui/core/Button'
import fire from '../../../constants/firebaseConfig';
import swal from "sweetalert";
import { formErrors } from "../../../common/errors";
import LeadTable from "./leadTable";

export default function LeadsComp(props) {
  const [tableData, setTableData] = React.useState([]);
  const [showLoader, setShowLoader] = React.useState(false);
  const [editData, setEditData] = React.useState({});
  const [leadFormOpen, setLeadFormOpen] = React.useState(false);
  useEffect(() => {
    props.fetchLeads()
  }, [])
  useEffect(() => {
    setShowLoader(props.showLoader)
    if (props.type == 'PRODUCT_LIST_SUCCESS') {

      setTableData(props.data)
    }
    else if (props.type == 'PRODUCT_LIST_FAILED') {
      swal({
        icon: "error",
        text: formErrors.networkError,
        timer: 2000
      });
    }
    else if (props.type == 'ADD_PRODUCT_SUCCESS') {

      handleLeadFormToggle('add')
      props.fetchLeads()

    }
    else if (props.type == 'ADD_PRODUCT_FAILED') {
      swal({
        icon: "error",
        text: formErrors.networkError,
        timer: 2000
      });
    }
    else if (props.type == 'DELETE_PRODUCT_SUCCESS') {

      props.fetchLeads()

    }
    else if (props.type == 'DELETE_PRODUCT_FAILED') {
      swal({
        icon: "error",
        text: formErrors.networkError,
        timer: 2000
      });

    }
    else if (props.type == 'EDIT_PRODUCT_SUCCESS') {

      props.fetchLeads();
      handleLeadFormToggle('add')

    }
    else if (props.type == 'EDIT_PRODUCT_FAILED') {
      swal({
        icon: "error",
        text: formErrors.networkError,
        timer: 2000
      });
    }

  }, [props.data, props.type])


  const handleLeadFormToggle = (param) => {
    if (param == 'add') {
      setEditData({})
    }
    setLeadFormOpen(!leadFormOpen);
  };
  const deleteLead = (data) => {
    props.deleteLead(data.id)
  }
  const editLead = (data) => {
    setEditData(data);
    handleLeadFormToggle('edit');
  }


  const addLead = (name, phone, address, age, pincode, range) => {
    props.addLead({ name, phone, address, age, pincode, range })

  }
  const callEditApi = (id, name, phone, address, age, pincode, range) => {
    props.editLead({ id, name, phone, address, age, pincode, range })
  }

  return (
    <GridContainer id="manageProductsComp">
      <Loader showLoader={showLoader} />
      <SideDrawer open={leadFormOpen} handleDrawerToggle={handleLeadFormToggle} title={editData.name ? 'Edit Lead' : "Add Lead"}>
        <LeadForm addLead={addLead} editData={editData} editLead={callEditApi} />
      </SideDrawer>

      <GridItem xs={12} sm={12} md={12}>
        <Card>
          <CardHeader color="info">
            <h4 >Telecaller Login</h4>
          </CardHeader>
          <CardBody>
            <GridContainer>
              <GridItem item xs={9}>
              </GridItem>
              <GridItem xs={3}>
                <Button
                  onClick={() => handleLeadFormToggle('add')}
                  color="primary"
                  endIcon={<AddIcon />}
                >
                  Add Lead
      </Button>
              </GridItem>

            </GridContainer>
            <LeadTable
              fixedHeader={true}
              tableHeaderColor="info"
              tabledata={tableData}
              totalCount={tableData.length}
              deleteLead={deleteLead}
              editLead={editLead}
            />
          </CardBody>
        </Card>
      </GridItem>

    </GridContainer>
  );
}
