import { connect } from "react-redux";
import LeadsComp from "../components/homeComp";
import { addProduct, deleteProduct, editProduct, fetchProductList } from "../homeActions";

const mapStateToProps = ({ homeReducer }) => ({
  data: homeReducer.data,
  type: homeReducer.type,
  error: homeReducer.error,
  showLoader:homeReducer.showLoader
});

const mapDispatchToProps = dispatch => ({
  fetchLeads: input => dispatch(fetchProductList(input)),
  addLead: input => dispatch(addProduct(input)),
  deleteLead: input => dispatch(deleteProduct(input)),
  editLead: input => dispatch(editProduct(input)),
});

export default connect(
    mapStateToProps,
    mapDispatchToProps
  )(LeadsComp);
