import {

  PRODUCT_LIST_REQUESTED,
  PRODUCT_LIST_SUCCESS,
  PRODUCT_LIST_FAILED,
  ADD_PRODUCT_REQUESTED,
  ADD_PRODUCT_SUCCESS,
  ADD_PRODUCT_FAILED,
  DELETE_PRODUCT_REQUESTED,
  DELETE_PRODUCT_SUCCESS,
  DELETE_PRODUCT_FAILED,
 
  } from "./homeActions";
  
  const initialState = {
     showLoader: true,
  };
  
  const homeReducer = (state = initialState, { type, payload }) => {
    switch (type) {
    
      case PRODUCT_LIST_REQUESTED:
        return { ...state, showLoader: true, type };
      case PRODUCT_LIST_SUCCESS:
        return {
          ...state,
          showLoader: false,
          type,
          data: payload
        };
      case PRODUCT_LIST_FAILED:
        return {
          ...state,
          showLoader: false,
          type,
          error: payload,
        };
        case ADD_PRODUCT_REQUESTED:
        return { ...state, showLoader: true, type };
      case ADD_PRODUCT_SUCCESS:
        return {
          ...state,
          showLoader: false,
          type,
          data: payload
        };
      case ADD_PRODUCT_FAILED:
        return {
          ...state,
          showLoader: false,
          type,
          error: payload,
        };
        case DELETE_PRODUCT_REQUESTED:
          return { ...state, showLoader: true, type };
        case DELETE_PRODUCT_SUCCESS:
          return {
            ...state,
            showLoader: false,
            type,
            data: payload
          };
        case DELETE_PRODUCT_FAILED:
          return {
            ...state,
            showLoader: false,
            type,
            error: payload,
          };
         
      default:
        return { ...state, showLoader: false, type };
    }
  };
  export default homeReducer;
  