import { call, put, takeEvery, takeLatest } from "redux-saga/effects";
import * as firebase from 'firebase';

import {
  PRODUCT_LIST_REQUESTED,
  PRODUCT_LIST_SUCCESS,
  PRODUCT_LIST_FAILED,
  ADD_PRODUCT_REQUESTED,
  ADD_PRODUCT_SUCCESS,
  ADD_PRODUCT_FAILED,
  DELETE_PRODUCT_REQUESTED,
  DELETE_PRODUCT_SUCCESS,
  DELETE_PRODUCT_FAILED,
  EDIT_PRODUCT_REQUESTED,
  EDIT_PRODUCT_SUCCESS,
  EDIT_PRODUCT_FAILED,
 
} from "./homeActions";


const reqProductListApi = input => {
  return firebase.database().ref('leads').once('value').then(res => {
    let arr=[]
    res.forEach(item=>{
      arr.push({id:item.key,...item.val()})
    })

   return arr
  });
};

function* reqProductList(inputData) {
  try {
    const response = yield call(reqProductListApi, inputData.payload);
    yield put({
      type: PRODUCT_LIST_SUCCESS,
      payload: response
    });
  } catch (err) {
    yield put({
      type: PRODUCT_LIST_FAILED,
      payload: err
    });
  }
}
const addProductApi = input => {
 
  return firebase.
    database().
    ref('leads').
    push(input)
};

function* addProduct(inputData) {
  try {
    const response = yield call(addProductApi, inputData.payload);
    yield put({
      type: ADD_PRODUCT_SUCCESS,
      payload: response
    });
  } catch (err) {
    yield put({
      type: ADD_PRODUCT_FAILED,
      payload: err
    });
  }
}
const deleteProductApi = input => {
  return firebase.database().ref('leads').child(input).remove()
};

function* deleteProduct(inputData) {
  try {
    const response = yield call(deleteProductApi, inputData.payload);
    yield put({
      type: DELETE_PRODUCT_SUCCESS,
      payload: response
    });
  } catch (err) {
    yield put({
      type: DELETE_PRODUCT_FAILED,
      payload: err
    });
  }
}
const editProductApi = input => {

  return firebase.
  database().
  ref('leads').
  child(input.id).
  update(input)
};
function* editProduct(inputData) {
  try {
    const response = yield call(editProductApi, inputData.payload);
    yield put({
      type: EDIT_PRODUCT_SUCCESS,
      payload: response
    });
  } catch (err) {
    yield put({
      type: EDIT_PRODUCT_FAILED,
      payload: err
    });
  }
}

export default function* homeSaga() {
  yield takeLatest(PRODUCT_LIST_REQUESTED, reqProductList);
  yield takeLatest(ADD_PRODUCT_REQUESTED, addProduct);
  yield takeLatest(DELETE_PRODUCT_REQUESTED, deleteProduct);
  yield takeLatest(EDIT_PRODUCT_REQUESTED, editProduct);


}
