
import React, { useEffect } from "react";
import PropTypes from "prop-types";
import VisibilityIcon from '@material-ui/icons/Visibility';
// @material-ui/core components
import { makeStyles } from "@material-ui/core/styles";
import Table from "@material-ui/core/Table";
import TableHead from "@material-ui/core/TableHead";
import TableRow from "@material-ui/core/TableRow";
import TableBody from "@material-ui/core/TableBody";
import TableCell from "@material-ui/core/TableCell";
import EditIcon from '@material-ui/icons/Edit';
import DeleteIcon from '@material-ui/icons/Delete';


import IconButton from '@material-ui/core/IconButton';

import styles from "../../../assets/jss/material-dashboard-react/components/tableStyle.js";
import { TablePagination } from "@material-ui/core";
import ConfirmationDialogBox from "../../shared/DialogBox/confirmationDialogBox.js";

const useStyles = makeStyles(styles);



function EnhancedTableHead(props) {

  const headCells = [
    { id: 'id', numeric: false, disablePadding: false, label: 'ID' },
  
    { id: 'name', numeric: false, disablePadding: false, label: 'Name' },
    { id: 'phone', numeric: false, disablePadding: false, label: 'Phone No' },
    { id: 'age', numeric: true, disablePadding: true, label: 'Age' },
    { id: 'address', numeric: false, disablePadding: true, label: 'Address' },
    { id: 'pincode', numeric: true, disablePadding: true, label: 'Pincode' },
    { id: 'range', numeric: true, disablePadding: true, label: 'Income Range' },
    { id: 'actions', numeric: true, disablePadding: false, label: 'Actions' },
  ];

  return (
    <TableHead>
      <TableRow>

        {headCells.map((headCell) => (
          <TableCell
            key={headCell.id}
            align={'left'}
            style={{ fontWeight: '600', color: 'rgb(6,176,197)' }}
            padding={headCell.disablePadding ? 'none' : 'default'}
          >
            {headCell.label}
          </TableCell>
        ))}
      </TableRow>
    </TableHead>
  );
}

export default function LeadTable(props) {
  const classes = useStyles();
  const [tableData, setTableData] = React.useState(props.tabledata)
  const [page, setPage] = React.useState(0);
  const [deleteData, setDeleteData] = React.useState(null);
  const [deletePopupOpen, setDeletePopupOpen] = React.useState(false);
  const [rowsPerPage, setRowsPerPage] = React.useState(10);

  useEffect(() => {
    setTableData(props.tabledata)
  }, [props.tabledata])
  const handleDeletePopupToggle = (data) => {
    setDeletePopupOpen(!deletePopupOpen);
    if (data) {
      setDeleteData(data)
    } else {
      setDeleteData(null)
    }
  };
  const handleDeletePopupSuccess = () => {
    props.deleteLead(deleteData)
    setDeletePopupOpen(!deletePopupOpen);
  };




  const handleChangePage = (event, newPage) => {
    setPage(newPage);
  };
  const handleChangeRowsPerPage = (event) => {
    setRowsPerPage(parseInt(event.target.value, 10));
    setPage(0);
  };

  return (
    <div className={classes.tableResponsive}>
      <ConfirmationDialogBox
        confirmationDialogOpen={deletePopupOpen}
        title="Are you sure want to delete?"
        desc=""
        handleConfirmationDialogToggle={handleDeletePopupToggle}
        handleConfirmationDialogSuccess={handleDeletePopupSuccess}
      />
      <Table className={classes.table}>
        <EnhancedTableHead />
        <TableBody id="productTableBody">
          {tableData && tableData.length > 0 && tableData.slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage).map((row, index) => {
            return (
              <TableRow
                hover
                key={index}
                tabIndex={-1}
              >
                                <TableCell align="left" style={{ minWidth: '150px', maxWidth: '150px' }}>{row.id}</TableCell>
  
                <TableCell align="left" style={{ minWidth: '120px', maxWidth: '120px' }}>{row.name}</TableCell>
                <TableCell align="left" style={{ minWidth: '120px', maxWidth: '120px' }}>{row.phone}</TableCell>
                <TableCell align="left" padding="none" style={{ minWidth: '100px', maxWidth: '100px' }}>{row.age} yr</TableCell>
                <TableCell align="left" padding="none" style={{ minWidth: '150px', maxWidth: '150px' }}>{row.address}</TableCell>
                <TableCell align="left" padding="none" style={{ minWidth: '110px', maxWidth: '110px' }} >{row.pincode}</TableCell>

                <TableCell align="left" padding="none" style={{ minWidth: '120px', maxWidth: '120px' }}>{row.range.length>0 && `${row.range[0]}Lakh - ${row.range[1]}Lakh`}</TableCell>
                <TableCell align="left" padding="none" style={{ minWidth: '100px', maxWidth: '100px' }}>
                  <IconButton aria-label="upload picture" color="primary" component="span" onClick={() => props.editLead(row)}>
                    <EditIcon />
                  </IconButton>
                  <IconButton color="secondary" aria-label="upload picture" component="span" onClick={() => handleDeletePopupToggle(row)}>
                    <DeleteIcon />
                  </IconButton>
                </TableCell>

              </TableRow>
            );
          })}

        </TableBody>
      </Table>
      <TablePagination
        rowsPerPageOptions={[10, 20, 30]}
        component="div"
        count={tableData.length}
        rowsPerPage={rowsPerPage}
        onChangeRowsPerPage={handleChangeRowsPerPage}
        page={page}
        onChangePage={handleChangePage}
      />
    </div>
  );
}













