export const PRODUCT_LIST_REQUESTED = "PRODUCT_LIST_REQUESTED";
export const PRODUCT_LIST_SUCCESS = "PRODUCT_LIST_SUCCESS";
export const PRODUCT_LIST_FAILED = "PRODUCT_LIST_FAILED";

export const fetchProductList = payload => ({
  type: PRODUCT_LIST_REQUESTED,
  payload
});

export const ADD_PRODUCT_REQUESTED = "ADD_PRODUCT_REQUESTED";
export const ADD_PRODUCT_SUCCESS = "ADD_PRODUCT_SUCCESS";
export const ADD_PRODUCT_FAILED = "ADD_PRODUCT_FAILED";

export const addProduct = payload => ({
  type: ADD_PRODUCT_REQUESTED,
  payload
});

export const DELETE_PRODUCT_REQUESTED = "DELETE_PRODUCT_REQUESTED";
export const DELETE_PRODUCT_SUCCESS = "DELETE_PRODUCT_SUCCESS";
export const DELETE_PRODUCT_FAILED = "DELETE_PRODUCT_FAILED";

export const deleteProduct = payload => ({
  type: DELETE_PRODUCT_REQUESTED,
  payload
});
export const EDIT_PRODUCT_REQUESTED = "EDIT_PRODUCT_REQUESTED";
export const EDIT_PRODUCT_SUCCESS = "EDIT_PRODUCT_SUCCESS";
export const EDIT_PRODUCT_FAILED = "EDIT_PRODUCT_FAILED";

export const editProduct = payload => ({
  type: EDIT_PRODUCT_REQUESTED,
  payload
});

