
import React, { useEffect } from 'react';
import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';
import Slider from '@material-ui/core/Slider';

import Grid from '@material-ui/core/Grid';
import Typography from '@material-ui/core/Typography';
import { makeStyles } from '@material-ui/core/styles';
import { formErrors } from '../../../common/errors';
import swal from 'sweetalert';
import { regex } from '../../../common/regex';

const useStyles = makeStyles((theme) => ({
    paper: {
        marginTop: theme.spacing(2),
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'center',
    },

    form: {
        width: '100%', // Fix IE 11 issue.
        marginTop: theme.spacing(0),
    },
    submit: {
        margin: theme.spacing(2, 0),
    },
}));
export default function LeadForm(props) {
    const classes = useStyles();
    const [id, setId] = React.useState('');
    const [phone, setPhone] = React.useState('');
    const [name, setName] = React.useState('');
    const [address, setAddress] = React.useState('');
    const [pincode, setPincode] = React.useState([]);
    const [age, setAge] = React.useState('');
    const [range, setRange] = React.useState([6, 10]);

    const resetForm = () => {
        setId('');
        setPhone('');
        setName('');
        setAddress('');
        setPincode('');
        setAge('');
        setRange([6, 10])

    }

    useEffect(() => {


        props.editData.name && setName(props.editData.name);
        props.editData.id && setId(props.editData.id);
        props.editData.age && setAge(props.editData.age);
        props.editData.phone && setPhone(props.editData.phone);
        props.editData.address && setAddress(props.editData.address);
        props.editData.pincode && setPincode(props.editData.pincode);
        props.editData.range && setRange(props.editData.range);

    }, [props.editData])

    const checkValidation = () => {
        if (!name ||
            !phone ||
            !address ||
            !age ||
            !pincode) {
            return false
        }
        if(!phone.match(regex.mobileNumber)){
            return false
        }
        return true
    }
    const addLead = () => {
        if (checkValidation()) {
            props.addLead(name, phone, address, age, pincode, range)
        }
        else if(!phone.match(regex.mobileNumber)){
            swal({
                icon: "info",
                text: formErrors.mobileValidity,
                timer: 5000
            });
        }
        else {
            swal({
                icon: "info",
                text: formErrors.fillAllFieldsError,
                timer: 5000
            });
        }
    }
    const editLead = () => {
        if (id && checkValidation()) {
            props.editLead(id, name, phone, address, age, pincode, range)
        }
        else if(!phone.match(regex.mobileNumber)){
            swal({
                icon: "info",
                text: formErrors.mobileValidity,
                timer: 5000
            });
        }
        else {
            swal({
                icon: "info",
                text: formErrors.fillAllFieldsError,
                timer: 5000
            });
        }
    }
    return (
        <div>


            <form className={classes.form} noValidate>
                <Grid container spacing={2}>
                    <Grid item xs={12}>
                        <TextField
                            variant="outlined"
                            style={{ marginTop: '10px' }}
                            required
                            fullWidth
                            label="Phone no."
                            name="phone"
                            value={phone}
                            type="number"
                            onChange={(e) => setPhone(e.target.value)}
                            className={classes.autocomplete}
                        />
                    </Grid>
                    <Grid item xs={12}>
                        <TextField
                            variant="outlined"

                            required
                            fullWidth
                            label="Name"
                            name="name"
                            value={name}
                            onChange={(e) => setName(e.target.value)}
                            className={classes.autocomplete}
                        />
                    </Grid>
                    <Grid item xs={12}>
                        <TextField
                            variant="outlined"
                            required
                            fullWidth
                            onChange={(e) => setAddress(e.target.value)}
                            label="Address"
                            name="address"
                            value={address}
                            className={classes.autocomplete}
                        />
                    </Grid>
                    <Grid item xs={12}>
                        <TextField
                            variant="outlined"

                            required
                            fullWidth
                            label="Pincode"
                            name="pincode"
                            value={pincode}
                            type="number"
                            onChange={(e) => setPincode(e.target.value)}
                            className={classes.autocomplete}
                        />
                    </Grid>
                    <Grid item xs={12}>
                        <TextField
                            variant="outlined"

                            required
                            fullWidth
                            label="Age"
                            name="age"
                            value={age}
                            type="number"
                            onChange={(e) => setAge(e.target.value)}
                            className={classes.autocomplete}
                        />
                    </Grid>
                    <Typography id="discrete-slider-custom" gutterBottom style={{ paddingLeft: '15px' }}>
                        Income Range(Lakhs)
</Typography>
                    <Grid item xs={12}>
                        <Slider
                            className={{ width: '100%' }}
                            value={range}
                            onChange={(event, newValue) => setRange(newValue)}
                            valueLabelDisplay="auto"
                            aria-labelledby="range-slider"
                            getAriaValueText={() => `${range}Rs`}
                        />
                    </Grid>


                </Grid>
                {props.editData.name ? <Button
                    type="button"
                    fullWidth
                    variant="contained"
                    color="primary"
                    className={classes.submit}
                    onClick={() => editLead()}
                >
                    Save
          </Button> : <Button
                        type="button"
                        fullWidth
                        variant="contained"
                        color="primary"
                        className={classes.submit}
                        onClick={() => addLead()}
                    >
                        Add Lead
          </Button>
                }
                <Button
                    type="button"
                    fullWidth
                    variant="contained"
                    color="primary"
                    onClick={resetForm}
                >
                    Reset
          </Button>

            </form>
        </div>
    );
}
